/* src/App.css */
.box{
    margin-left: 400px;
    
}
.game {
    display: flex;
    flex-direction: row;
    color: blue;
  }
  
  .game-board {
    margin: 20px;
    color: aliceblue;
   
  }
  
  .board-row:after {
    clear: both;
    content: '';
    display: table;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 2rem;
    font-weight: bold;
    line-height: 34px;
    height: 100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 100px;
  }
  
  .status {
    margin-bottom: 10px;
  }
  